.button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 49px;
  padding: 0 10px;
  font-family: "SF UI Display", "Arial", sans-serif;
  text-transform: uppercase;
  border-width: 1px;
  border-style: solid;
  border-radius: 9999px;
}
.button--primary {
  min-height: 42px;
  font-size: 13px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.045em;
  border: none;
  background-color: #ff970a;
  box-shadow: 0 2.4px 12px rgba(255, 151, 10, 0.41);
}
.button--2 {
  font-weight: 500;
  color: #ff970a;
  letter-spacing: 0.08em;
  border-color: rgba(255, 255, 255, 0.3);
  background-color: #000000;
}
.button--3 {
  min-height: 42px;
  font-weight: 500;
  line-height: 13px;
  color: #a1a1a1;
  letter-spacing: 0.07em;
  border-color: #7c7571;
  background-color: #000000;
}
.button--arrow::after {
  content: "";
  width: 19px;
  height: 12px;
  margin-left: 8px;
  background-image: url("../img/icon-arrow-button.svg");
  background-repeat: no-repeat;
}
@media (min-width: 1150px) {
  .button {
    cursor: pointer;
    transition: 0.3s;
  }
  .button--primary {
    min-height: 49px;
    font-size: 15px;
  }
  .button--primary:hover {
    transform: translateY(-3px);
  }
  .button--2:hover {
    border-color: #ff970a;
  }
  .button--3 {
    min-height: 46px;
  }
  .button--3:hover {
    border-color: #ff970a;
  }
  .button--arrow:hover {
    transform: translateY(-3px);
  }
}

.input {
  width: 100%;
  max-width: unset;
  padding: 13px 28px;
  font-size: 13px;
  line-height: 1;
  letter-spacing: 0.045em;
  color: #ffffff;
  border: none;
  border-radius: 40px;
  background-color: #181818;
}
.input::placeholder {
  line-height: 1;
}
.input:focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff970a;
}
@media (min-width: 1150px) {
  .input {
    padding: 17px 26px;
    font-size: 15px;
  }
}

.heading {
  margin: 0;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.26;
}
.heading--3 {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}
@media (min-width: 768px) {
  .heading {
    font-size: 32px;
    line-height: 1;
  }
  .heading--3 {
    font-size: 24px;
  }
}
@media (min-width: 1150px) {
  .heading {
    font-size: 60px;
  }
  .heading--3 {
    font-size: 30px;
    font-weight: 600;
  }
}

.page {
  height: 100%;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "SF UI Display", "Arial", sans-serif;
  line-height: 1;
  color: #ffffff;
  background-color: #000000;
}

.page-header {
  display: grid;
  grid-template-columns: 1fr repeat(2, auto);
  padding: 10px 23px;
}
@media (min-width: 768px) {
  .page-header {
    grid-template-rows: auto;
  }
}
@media (min-width: 1150px) {
  .page-header {
    z-index: 1;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    width: 100%;
    padding: 0;
  }
}

@media (min-width: 768px) {
  .page-header__top-line {
    grid-column: 1/4;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .page-header__top-line {
    padding: 10px 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1440px) {
  .page-header__top-line {
    padding: 10px 0;
  }
}

@media (min-width: 768px) {
  .page-header__top-wrapper {
    display: grid;
    grid-template-columns: 1fr auto;
    width: 50%;
  }
}
@media (min-width: 1150px) {
  .page-header__top-wrapper {
    grid-template-columns: auto 1fr auto;
    justify-content: space-between;
    justify-items: center;
    align-items: center;
    width: 100%;
    max-width: 1348px;
    margin: 0 auto;
  }
}

.page-header__mobile-buttons {
  display: flex;
  align-items: center;
  margin-right: 27px;
}
@media (min-width: 768px) {
  .page-header__mobile-buttons {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .page-header__mobile-buttons {
    grid-column: 1/4;
    grid-row: unset;
    justify-content: flex-end;
    width: 100%;
    margin: 0 auto;
    padding: 20px 30px 0;
  }
}
@media (min-width: 1440px) {
  .page-header__mobile-buttons {
    max-width: 1308px;
    padding: 30px 0 0;
  }
}

.page-header__mobile-button:not(:last-child) {
  margin-right: 10px;
}
@media (min-width: 1150px) {
  .page-header__mobile-button:not(:last-child) {
    margin-right: 14px;
  }
}

.page-header__button_invest {
  display: none;
}
@media (min-width: 768px) {
  .page-header__button_invest {
    display: flex;
    min-width: 211px;
    transform: translateX(50%);
  }
}
@media (min-width: 1150px) {
  .page-header__button_invest {
    transform: none;
  }
}

.page-header__toggle {
  position: relative;
  width: 37px;
  height: 37px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.page-header__toggle::before, .page-header__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  height: 2px;
  border-radius: 1px;
  background-color: #ffffff;
}
.page-header__toggle::before {
  top: 16px;
  width: 35px;
}
.page-header__toggle::after {
  top: 25px;
  width: 22px;
}
@media (min-width: 768px) {
  .page-header__toggle {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .page-header__toggle {
    display: none;
  }
}

.mobile-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  padding: 0;
  color: #4c4c4c;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 1150px) {
  .mobile-button {
    width: 30px;
    height: 30px;
    background-size: auto 20px;
    cursor: pointer;
    transition: 0.3s;
  }
  .mobile-button:hover {
    color: #ffffff;
  }
}

@media (min-width: 1150px) {
  .mobile-button__icon {
    width: 30px;
    height: 30px;
    background-size: auto 20px;
  }
  .mobile-button__icon--play {
    width: 23px;
    height: 23px;
  }
  .mobile-button__icon--corners {
    width: 21px;
    height: 21px;
  }
  .mobile-button__icon--sound {
    width: 28px;
    height: 21px;
  }
}

.main-navigation {
  display: none;
}
@media (min-width: 1150px) {
  .main-navigation {
    display: block;
    width: max-content;
  }
}

.main-navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 800px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 600;
  text-transform: uppercase;
}

.main-navigation__item:not(:last-child) {
  margin-right: 40px;
}

.main-navigation__link {
  color: #ffffff;
  transition: 0.3s;
}
.main-navigation__link:hover {
  color: #ff970a;
}

@media (min-width: 1150px) {
  .logo__image--footer {
    width: 168px;
    height: 64px;
  }
}
@media (min-width: 1440px) {
  .logo__image {
    width: 165px;
    height: 62px;
  }
}

.page-footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 15px;
  justify-items: center;
  padding: 62px 17px 74px;
}
@media (min-width: 768px) {
  .page-footer {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: min-content 1fr repeat(3, auto);
    justify-items: start;
    padding: 62px 30px 74px;
  }
}
@media (min-width: 1150px) {
  .page-footer {
    grid-template-columns: repeat(4, 1fr);
    max-width: 1440px;
    padding: 72px 45px 112px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .page-footer {
    grid-template-columns: 420px repeat(3, 1fr);
  }
}

.page-footer__logo,
.page-footer__socials,
.page-footer__contacts,
.page-footer__big-navigation,
.page-footer__request,
.page-footer__cookies-info {
  grid-column: span 2;
}
@media (min-width: 768px) {
  .page-footer__logo,
.page-footer__socials,
.page-footer__contacts,
.page-footer__big-navigation,
.page-footer__request,
.page-footer__cookies-info {
    grid-column: span 1;
  }
}

.page-footer__logo {
  margin-bottom: 16px;
}
@media (min-width: 1150px) {
  .page-footer__logo {
    margin-bottom: 23px;
  }
}

.page-footer__socials {
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .page-footer__socials {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

.page-footer__contacts {
  margin-bottom: 50px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-footer__contacts {
    grid-row: 4/5;
    text-align: left;
  }
}
@media (min-width: 1150px) {
  .page-footer__contacts {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}

.page-footer__phone-number {
  margin: 0 0 3px;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.08em;
  color: #ffffff;
  transition: 0.3s;
}
@media (min-width: 1150px) {
