    font-size: 27px;
  }
}

.page-footer__phone-caption {
  margin: 0 0 25px;
  font-size: 13px;
  line-height: 1.45;
  color: #c0c0c0;
}

.page-footer__email {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.08em;
  text-decoration: underline;
  text-decoration-color: transparent;
  color: #ff970a;
  transition: 0.3s;
}
@media (min-width: 1150px) {
  .page-footer__email {
    font-size: 18px;
  }
  .page-footer__email:hover {
    text-decoration-color: #ff970a;
  }
}

.page-footer__navigation {
  place-self: start;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .page-footer__navigation {
    grid-row: span 2;
    padding-top: 15px;
    margin-bottom: 33px;
  }
}

.page-footer__big-navigation {
  margin-bottom: 45px;
}
@media (min-width: 768px) {
  .page-footer__big-navigation {
    grid-column: 2/4;
    grid-row: 3/4;
  }
}
@media (min-width: 1150px) {
  .page-footer__big-navigation {
    padding-top: 8px;
    grid-column: 4/5;
    grid-row: 1/3;
    margin-bottom: 33px;
