}

.page-footer__request {
  width: 100%;
  margin-bottom: 42px;
}
@media (min-width: 768px) {
  .page-footer__request {
    grid-column: 2/4;
    grid-row: 4/6;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(2, min-content);
