.page-footer__input:not(:last-of-type) {
  margin-bottom: 10px;
}
.page-footer__input:last-of-type {
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .page-footer__input:not(:last-of-type) {
    grid-column: span 2;
    width: 70%;
    margin-bottom: 22px;
  }
  .page-footer__input:last-of-type {
    grid-column: 1/3;
    grid-row: 2/3;
    margin-bottom: 0;
    padding-right: 55%;
  }
}

.page-footer__button-submit {
  min-width: 195px;
}
@media (min-width: 768px) {
  .page-footer__button-submit {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .page-footer__button-submit:hover {
    transform: translateY(0);
  }
}

.page-footer__cookies-info {
  margin: 0;
  font-size: 11px;
  line-height: 1.45;
  color: #c0c0c0;
}
@media (min-width: 768px) {
  .page-footer__cookies-info {
    grid-row: 5/6;
  }
}
@media (min-width: 1150px) {
  .page-footer__cookies-info {
    grid-column: 1/3;
    grid-row: 4/5;
    font-size: 15px;
  }
}

.socials__list {
