.intro {
  padding-top: 5px;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url("../img/bg-intro-mobile.png");
  background-position: center bottom;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .intro {
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url("../img/bg-intro-desktop.png");
    background-position: left bottom;
    background-size: contain;
  }
}
@media (min-width: 1150px) {
  .intro {
    margin-top: -120px;
    padding-top: 232px;
    background-size: auto 100%;
  }
}
@media (min-width: 1440px) {
  .intro {
    max-width: 1440px;
    margin: -146px auto 0;
  }
}

@media (min-width: 1150px) {
  .intro__wrapper {
    display: grid;
    grid-template-columns: 1fr auto;
    padding: 0 47px;
  }
}

.intro__profit {
  margin: 0 0 171px;
  padding: 0 23px;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.24;
}
@media (min-width: 768px) {
  .intro__profit {
    width: 40%;
    padding: 0 0 0 50px;
    font-size: 4.5vw;
  }
}
@media (min-width: 1150px) {
  .intro__profit {
    width: auto;
    max-width: 689px;
    margin: 0 0 58px;
    padding: 0;
    font-size: 73px;
  }
}

.intro__percent {
  font-weight: 600;
  color: #ff970a;
}

.intro__socials {
  display: none;
}
@media (min-width: 1150px) {
  .intro__socials {
    display: flex;
  }
}

.intro__video {
  position: relative;
  display: grid;
  grid-template-columns: auto 75px;
  column-gap: 15px;
  align-items: center;
  width: 61%;
  max-width: 270px;
  min-height: 56px;
  margin-left: auto;
  padding-left: 15px;
  background-image: linear-gradient(124.94deg, #2c2c2c -320.88%, rgba(200, 200, 200, 0.2) 127.52%);
  border-top-left-radius: 12px;
  backdrop-filter: blur(2.55466px);
}
.intro__video::after {
  content: "";
  position: absolute;
  top: 55%;
  right: 63px;
  width: 18px;
  height: 23px;
  background-image: url("../img/icon-play.svg");
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .intro__video {
    grid-template-columns: auto 155px;
    width: 40%;
    max-width: 500px;
    min-height: 89px;
    padding-left: 15px;
    background-image: linear-gradient(124.94deg, #2c2c2c -320.88%, rgba(200, 200, 200, 0.2) 127.52%);
    background-size: contain;
    border-top-left-radius: 22px;
  }
  .intro__video::after {
    right: 135px;
    width: 26px;
